html,
body {
  margin: 0;
  padding: 0;
  background-color: rgb(213, 213, 213) !important;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.taskTracker {
  margin: 3rem 1rem 0 1rem;
}

.button-default {
  width: 100%;
  border: 0;
  height: 35px;
  border-radius: 0;
  font-size: 15px;
  font-weight: 600;
  color: #eee;
  border: 2px solid #000;
  border-style: outset;
  &:active,
  &:focus {
    outline: none;
    border: 2px solid #000;
  }
}

.add-task-btn {
  @extend .button-default;
  background-color: #059669;
  &:hover {
    background-color: #10b981;
    color: #000;
    border: 2px solid #000;
  }
}

.clear-task-btn {
  @extend .button-default;
  margin: 5px 0 5px 0;
  background-color: #dc2626;
  &:hover {
    background-color: #ef4444;
    color: #000;
    border: 2px solid #000;
  }
}

.input-group-text {
  border: 2px solid #000 !important;
  border-right: none !important;
  border-radius: 0 !important;
  padding: 0 !important;
}

.input-group {
  .form-control {
    border: 2px solid #000 !important;
    border-left: none !important;
    border-radius: 0;
    &:active,
    &:focus {
      outline: none !important;
      outline-width: 0 !important;
      box-shadow: none;
    }
  }
  .input-group-text {
    width: 130px;
    display: flex;
    justify-content: center;
  }
}

.textarea {
  resize: none;
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #f5f5f5;
  }
  &::-webkit-scrollbar {
    width: 6px;
    background-color: #f5f5f5;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #000000;
  }
}

.legend {
  border: 1px solid #000;
  margin: 15px 0 5px 0;
  padding: 0 10px;
}

.flex-container {
  display: flex;
  .label {
    margin: 0;
  }
  .flex-item {
    display: flex;
    margin: 0 auto;
    .box-pending {
      margin: 5px 10px;
      width: 15px;
      height: 15px;
      background-color: #f59e0b;
    }
    .box-success {
      margin: 5px 10px;
      width: 15px;
      height: 15px;
      background-color: #10b981;
    }
  }
}

.task-card {
  background-color: transparent;
  margin: 0;
  padding: 5px;
  position: relative;
  border-radius: 0;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);

  border-left: 4px solid #000;

  &.pending {
    border-left: 4px solid #f59e0b;
  }
  &.complete {
    border-left: 4px solid #10b981;
  }

  .title {
    font-size: 17px;
    letter-spacing: 0.7px;
    font-weight: 600;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 25px 0;
    transition: all 0.5s ease;
  }

  .description {
    font-size: 13px;
    padding-left: 25px !important;
    opacity: 0;
    max-height: 0;
    overflow-y: hidden;
    transition: all 0.5s ease;
    padding: 0 10px 0;
    white-space: pre-line;
    color: rgb(90, 90, 90);
  }

  .remove-btn {
    border: none;
    padding: 0;
    margin: 0;
    width: 25px;
    height: 25px;
    background-color: transparent;
    color: red;
    outline: none;
    position: absolute;
    right: 0;
    top: 7px;
  }

  .toggle-btn {
    border: none;
    padding: 0 10px;
    margin: 0;
    background-color: transparent;
    color: #000;
    outline: none;
    transition: all 0.3s ease;
    position: absolute;
    top: 18px;
    left: 0px;
    transform: translateY(-50%);
    font-size: 28px;
  }

  &[toggle="open"] {
    .title {
      margin-bottom: 0px;
    }
    .description {
      opacity: 1;
      max-height: 100px;
    }
    .toggle-btn {
      transform: translateY(-50%) rotate(-180deg);
    }
  }
}

.alert-container {
  background-color: #414141;
  width: 50%;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center !important;
  height: 40px;
  border-radius: 0;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  border: 1px solid #000;
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
  span {
    font-size: 15px;
    font-weight: 500;
    color: #fff;
  }
}
